/* -----------------------------------------
   Input sizes
----------------------------------------- */
.input-mini {
  width: 60px !important; }

.input-small {
  width: 90px !important; }

.input-medium {
  width: 150px !important; }

.input-large {
  width: 210px !important; }

.input-xlarge {
  width: 270px !important; }

.input-block {
  width: 100%;
  display: block; }

/* -----------------------------------------
   Touchspin - spinner
----------------------------------------- */
.bootstrap-touchspin .input-group-btn-vertical {
  position: relative;
  white-space: nowrap;
  width: 1%;
  vertical-align: middle;
  display: table-cell; }

.bootstrap-touchspin .input-group-btn-vertical > .btn {
  display: block;
  float: none;
  width: 100%;
  max-width: 100%;
  padding: 8px 10px;
  margin-left: -1px;
  position: relative;
  border: 1px solid #cacaca; }

.bootstrap-touchspin .input-group-btn-vertical .bootstrap-touchspin-up {
  border-radius: 0;
  border-top-right-radius: 4px; }

.bootstrap-touchspin .input-group-btn-vertical .bootstrap-touchspin-down {
  margin-top: -2px;
  border-radius: 0;
  border-bottom-right-radius: 4px; }

.bootstrap-touchspin .input-group-btn-vertical i {
  position: absolute;
  top: 3px;
  left: 5px;
  font-size: 9px;
  font-weight: normal;
  color: #808080; }

.bootstrap-touchspin .bootstrap-touchspin-prefix {
  border-right: 0; }

.bootstrap-touchspin .bootstrap-touchspin-postfix {
  border-left: 0; }

/* -----------------------------------------
   Bootstrap typeahead
----------------------------------------- */
.tt-input,
.tt-hint {
  width: 260px;
  height: 24px;
  padding: 4px 6px;
  font-size: 16px;
  line-height: 24px;
  border: 1px solid #efefef;
  border-radius: 3px;
  outline: none; }

.tt-input {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); }

.tt-hint {
  color: #999999; }

.tt-dropdown-menu {
  width: 250px;
  margin-top: 5px;
  padding: 5px 0;
  background-color: #ffffff;
  border: 1px solid #efefef;
  border-radius: 3px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); }

.tt-suggestion {
  padding: 3px 10px;
  font-size: 14px;
  line-height: 24px; }

.tt-suggestion.tt-cursor {
  color: #1a1a1a;
  background-color: #f7f9fe; }

.tt-suggestion p {
  margin: 0; }

/* -----------------------------------------
   Flot charts
----------------------------------------- */
#flotTip {
  /* white-space: nowrap;
          display: inline-block; */
  max-width: 150px;
  padding: 3px 8px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  background-color: #344154 !important;
  border-radius: 3px !important;
  border-width: 0 !important;
  z-index: 100;
  opacity: 0.8;
  filter: alpha(opacity=80);
  font-family: "Roboto";
  font-size: 12px; }

.tickLabel {
  font-family: "Roboto";
  font-size: 11px;
  text-align: center;
  font-weight: normal; }

/* -----------------------------------------
   Easy pie charts
----------------------------------------- */
.easyPieChart {
  position: relative;
  text-align: center; }

.easyPieChart canvas {
  position: absolute;
  top: 0;
  left: 0; }

.pie-charts {
  display: inline-block;
  text-align: center;
  padding: 0 12px;
  margin-bottom: 20px; }

.pie-charts .easyPieChart {
  font-weight: bold; }

.pie-charts div.label {
  color: #333333;
  font-size: 100%;
  width: 100%;
  float: left;
  margin-top: 10px; }

.pie-charts.red-pie .easyPieChart {
  color: #db5565; }

.pie-charts.red-pie div.label {
  color: #db5565; }

.pie-charts.green-pie .easyPieChart {
  color: #51bf87; }

.pie-charts.green-pie div.label {
