.pie-charts.yellow-pie .easyPieChart {
  color: #f4ad49; }

.pie-charts.yellow-pie div.label {
  color: #f4ad49; }

/* -----------------------------------------
  Gauges
----------------------------------------- */
.glow-gauge,
.gauge {
  display: inline-block;
