  padding: 0 12px;
  margin-bottom: 20px; }

/* -----------------------------------------
   Vectormaps
----------------------------------------- */
.jvectormap-label {
  position: absolute;
  display: none;
  border: solid 1px #344154;
  border-radius: 3px;
  background: #344154;
  color: #ffffff;
  font-family: sans-serif, Verdana;
  font-size: smaller;
  padding: 3px 5px;
  z-index: 999; }

.jvectormap-zoomin,
.jvectormap-zoomout {
  position: absolute;
  left: 10px;
  border-radius: 3px;
  background: #344154;
  padding: 3px;
  color: white;
  width: 18px;
  height: 18px;
  cursor: pointer;
  line-height: 10px;
  text-align: center; }

.jvectormap-zoomin {
  top: 10px; }

.jvectormap-zoomout {
  top: 30px; }

/* -----------------------------------------
   Highlight rows (tables, list and etc )
----------------------------------------- */
.highlight {
  background: #fffed5; }

.highlight td {
  background: #fffed5; }

.highlight:hover {
  background: #fffdbc !important; }

/* -----------------------------------------
   Seperator
----------------------------------------- */
